@import "../utils/Variables.scss";
/* TwoContainerComponent.css */

.twoContainer {
  display: flex;
}

.leftContainer {
  flex: 1;
  padding: 20px;
  background-color: #e0e0e0;
}

.portfolioBtn {
  padding: 10px;
  margin-top: 10px;
  background-color: #4285f4;
  color: white;
  cursor: pointer;
}

.rightContainer {
  flex: 1;
  padding: 20px;
  background-color: #f0f0f0;
}

.body {
 
  input[type="text"] {
    border: 1px solid rgba(122, 118, 122, 1);
    border-radius: 4px;
    width: 200px;
    height: 30px;
    margin-left: 3vw;
    margin-bottom: 10px;

    &:focus {
      outline: 1px solid #ddb532;
    }  
  } 
  
}
@media (max-width: 768px) {
  /* Styles for phone devices */
  .containerBox {
    width: 60vw;
    margin-top: 10vw;

   

  .body {
    input[type="text"] {
      width: 80px;
      margin-left: 5px;
      margin-bottom: 5px;
    }       
                
    }
   
  }
}

@media (min-width: 769px) and (max-width: 1024px) {

  .containerBox {
    width: 80vw;
   
  }
}









